import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';

//additions
import {useEffect, useContext} from 'react'
import JwtAuthContext from '../../../../contexts/JwtAuthContext'
import PaypalButton from './PaypalButton'
import PaynowButton from './PaynowButton'

// material-ui
import {
    Box,
    Button,
    FormControl,
    FormControlLabel,
    Grid,
    RadioGroup,
    Radio,
    Stack,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableRow,
    Typography
} from '@material-ui/core';

// third-party
import CurrencyFormat from 'react-currency-format';

// project imports
import OrderSummary from './OrderSummary';
import AddressCard from './AddressCard';
import PaymentSelect from './PaymentSelect';
import ColorOptions from '../ColorOptions';
import PaymentOptions from './PaymentOptions';
import PaymentCard from './PaymentCard';
import AddPaymentCard from './AddPaymentCard';
import OrderComplete from './OrderComplete';
import SubCard from 'ui-component/cards/SubCard';
import Avatar from 'ui-component/extended/Avatar';
import { SNACKBAR_OPEN, SET_PAYMENT_METHOD, SET_PAYMENT_CARD } from 'store/actions';
import { gridSpacing } from 'store/constant';

// assets
import AddTwoToneIcon from '@material-ui/icons/AddTwoTone';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';

const prodImage = require.context('assets/images/e-commerce', true);

// product color select
function getColor(color) {
    return ColorOptions.filter((item) => item.value === color);
}

// ===========================|| CHECKOUT PAYMENT - MAIN ||=========================== //

const Payment = ({ checkout, onBack, onNext, handleShippingCharge, values}) => {

    //additions
    const [pushCheckout, setPushCheckout] = useState([])
    const {user, url} = useContext(JwtAuthContext)

     const cart = useSelector((state) => state.cart);

    const dispatch = useDispatch();

    const [type, setType] = React.useState(checkout.payment.type);
    const [payment, setPayment] = React.useState(checkout.payment.method);
    const [rows, setRows] = React.useState(checkout.products);
    const [cards, setCards] = React.useState(checkout.payment.card);

    const [open, setOpen] = React.useState(false);
    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const [complete, setComplete] = React.useState(false);

    const handleCompleteClose = () => {
        setComplete(false);
    };

    React.useEffect(() => {
        if (checkout.step > 2) {
            setComplete(true);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    React.useEffect(() => {
        setRows(checkout.products);
        setPushCheckout(checkout.products);
        console.log("Checkout Products Below")
        console.log(checkout)
    }, [checkout.products]);

    const cardHandler = (card) => {
        if (payment === 'card') {
            setCards(card);
            dispatch({ type: SET_PAYMENT_CARD, card });
        }
    };

    const handlePaymentMethod = (value) => {
        setPayment(value);
        dispatch({ type: SET_PAYMENT_METHOD, method: value });
    };

    const completeHandler = async () => {
        if (payment === 'card' && (cards === '' || cards === null)) {
            dispatch({
                type: SNACKBAR_OPEN,
                open: true,
                message: 'Select Payment Card',
                variant: 'alert',
                alertSeverity: 'error'
            });
        } else {
            onNext();
            setComplete(true);

            console.log("Called from paypal")

            checkout.products.map( async (product) => {
                const purchase_amount = product.price * product.quantity
                const response = await fetch(`${url}/api/add-order/`, {
                    method: 'POST',
                    headers: {
                        'Content-Type':'application/json'
                    },
                    body: JSON.stringify({
                        'payee_name': product.owner, 
                        'quantity': product.quantity,
                        'payment_type': values.payment_type,
                        'products': product.name,
                        'payer_email': user.email,
                        'payer_name': user.username,
                        'purchase_amount': purchase_amount,
                        'shipping_address': 'Address Now'
                    })
                    
                })

                console.log(response)
            })

            //CREATE ORDER
            /* 

            console.log('from Order Tab') */
        } 
    };

    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12} md={6} lg={8} xl={9}>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Stack>
                            <Typography variant="subtitle1">Delivery Options</Typography>
                            <FormControl>
                                <RadioGroup
                                    row
                                    aria-label="delivery-options"
                                    value={type}
                                    onChange={(e) => {
                                        setType(e.target.value);
                                        handleShippingCharge(e.target.value);
                                    }}
                                    name="delivery-options"
                                >
                                    <Grid container spacing={gridSpacing} alignItems="center" sx={{ pt: 2 }}>
                                        <Grid item xs={12} sm={6} md={12} lg={6}>
                                            <SubCard content={false}>
                                                <Box sx={{ p: 2 }}>
                                                    <FormControlLabel
                                                        value="free"
                                                        control={<Radio />}
                                                        label={
                                                            <Stack spacing={0.25}>
                                                                <Typography variant="subtitle1">Standard Delivery (Free)</Typography>
                                                                <Typography variant="caption">Delivered on Monday 8 Jun</Typography>
                                                            </Stack>
                                                        }
                                                        sx={{ '& .MuiSvgIcon-root': { fontSize: 32 } }}
                                                    />
                                                </Box>
                                            </SubCard>
                                        </Grid>
                                        <Grid item xs={12} sm={6} md={12} lg={6}>
                                            <SubCard content={false}>
                                                <Box sx={{ p: 2 }}>
                                                    <FormControlLabel
                                                        value="fast"
                                                        control={<Radio />}
                                                        label={
                                                            <Stack spacing={0.25}>
                                                                <Typography variant="subtitle1">Fast Delivery ($5.00)</Typography>
                                                                <Typography variant="caption">Delivered on Friday 5 Jun</Typography>
                                                            </Stack>
                                                        }
                                                        sx={{ '& .MuiSvgIcon-root': { fontSize: 32 } }}
                                                    />
                                                </Box>
                                            </SubCard>
                                        </Grid>
                                    </Grid>
                                </RadioGroup>
                            </FormControl>
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="subtitle1">Payment Options</Typography>
                    </Grid>
                    <Grid item xs={12} lg={6}>
                        <FormControl>
                            <RadioGroup
                                aria-label="delivery-options"
                                value={payment}
                                onChange={(e) => handlePaymentMethod(e.target.value)}
                                name="delivery-options"
                            >
                                <Grid container spacing={gridSpacing} alignItems="center">
                                    {PaymentOptions.map((item, index) => (
                                        <Grid item xs={12} key={index}>
                                            <PaymentSelect item={item} />
                                        </Grid>
                                    ))}
                                </Grid>
                            </RadioGroup>
                        </FormControl>
                        
                    </Grid>
                    <Grid item xs={12} lg={6}>
                        <PaynowButton rows={rows}/>
                    </Grid>
                    <Grid item xs={12} lg={6}>
                        <PaypalButton rows={rows} onPost={completeHandler}/>
                    </Grid>
                    {/* <Grid item xs={12} lg={6} sx={{ opacity: payment === 'card' ? 1 : 0.4 }}>
                        <SubCard
                            title="Add Your Card"
                            secondary={
                                <Button variant="contained" size="small" startIcon={<AddTwoToneIcon />} onClick={handleClickOpen}>
                                    Add Card
                                </Button>
                            }
                        >
                            <Grid container spacing={gridSpacing}>
                                <Grid item xs={12} xl={6}>
                                    <PaymentCard type="mastercard" cards={cards} cardHandler={cardHandler} />
                                </Grid>
                                <Grid item xs={12} xl={6}>
                                    <PaymentCard type="visa" cards={cards} cardHandler={cardHandler} />
                                </Grid>
                            </Grid>
                            <AddPaymentCard open={open} handleClose={handleClose} />
                        </SubCard>
                    </Grid> */}
                    <Grid item xs={12}>
                        <Grid container spacing={3} alignItems="center" justifyContent="space-between">
                            <Grid item>
                                <Button variant="string" startIcon={<KeyboardBackspaceIcon />} onClick={onBack}>
                                    Back
                                </Button>
                            </Grid>
                            <Grid item>
                                <Button variant="contained" onClick={completeHandler}>
                                    Complete Order
                                </Button>
                                <OrderComplete open={complete} handleClose={handleCompleteClose} />
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} md={6} lg={4} xl={3}>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Stack>
                            <Typography variant="subtitle1" sx={{ pb: 2 }}>
                                Cart Items
                            </Typography>
                            <TableContainer>
                                <Table sx={{ minWidth: 280 }} aria-label="simple table">
                                    <TableBody>
                                        {rows.map((row, index) => {
                                            const colorsData = row.color ? getColor(row.color) : false;
                                            return (
                                                <TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                                    <TableCell component="th" scope="row">
                                                        <Grid container alignItems="center" spacing={2}>
                                                            <Grid item>
                                                                <Avatar
                                                                    size="md"
                                                                    variant="rounded"
                                                                    src={row.image ? prodImage(`./${row.image}`).default : ''}
                                                                />
                                                            </Grid>
                                                            <Grid item>
                                                                <Stack spacing={0}>
                                                                    <Typography variant="subtitle1">{row.name}</Typography>
                                                                    <Stack direction="row" alignItems="center" spacing={1}>
                                                                        <Typography variant="subtitle2" sx={{ fontWeight: 500 }}>
                                                                            Size:{' '}
                                                                            <Typography variant="caption" component="span">
                                                                                {row.size}
                                                                            </Typography>
                                                                        </Typography>
                                                                        <Typography variant="caption" sx={{ fontSize: '1rem' }}>
                                                                            |
                                                                        </Typography>
                                                                        <Typography variant="subtitle2" sx={{ fontWeight: 500 }}>
                                                                            Color:{' '}
                                                                            <Typography variant="caption" component="span">
                                                                                {colorsData ? colorsData[0].label : 'Multicolor'}
                                                                            </Typography>
                                                                        </Typography>
                                                                    </Stack>
                                                                </Stack>
                                                            </Grid>
                                                        </Grid>
                                                    </TableCell>
                                                    <TableCell align="right">
                                                        <Typography variant="subtitle1">
                                                            <CurrencyFormat
                                                                decimalScale={2}
                                                                fixedDecimalScale
                                                                value={row.price * row.quantity}
                                                                displayType="text"
                                                                thousandSeparator
                                                                prefix="$"
                                                            />
                                                        </Typography>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        <OrderSummary checkout={checkout} />
                    </Grid>
                    <Grid item xs={12}>
                        <AddressCard single change address={checkout.billing} onBack={onBack} />
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    );
};

Payment.propTypes = {
    checkout: PropTypes.object,
    handleShippingCharge: PropTypes.func,
    onBack: PropTypes.func,
    onNext: PropTypes.func
};

export default Payment;
