import React from 'react';
import {useNavigate} from 'react-router-dom'
//Additions
import {useState, useEffect, useContext} from 'react'
import useAuth from 'hooks/useAuth'
import { useDispatch } from 'react-redux';
import { ADD_PRODUCTS, SNACKBAR_OPEN } from 'store/actions';

// material-ui
import { Button, Grid, MenuItem, TextField, Stack } from '@material-ui/core';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';


// project imports
import SubCard from 'ui-component/cards/SubCard';
import AnimateButton from 'ui-component/extended/AnimateButton';
import { gridSpacing } from 'store/constant';

// assets
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

 import loader from 'assets/images/ring-loader.svg';

// select options
const currencies = [
    {
        value: 'Washington',
        label: 'Washington'
    },
    {
        value: 'India',
        label: 'India'
    },
    {
        value: 'Africa',
        label: 'Africa'
    },
    {
        value: 'New-York',
        label: 'New York'
    },
    {
        value: 'Malaysia',
        label: 'Malaysia'
    }
];

const experiences = [
    {
        value: 'Startup',
        label: 'Startup'
    },
    {
        value: '2-year',
        label: '2 year'
    },
    {
        value: '3-year',
        label: '3 year'
    },
    {
        value: '4-year',
        label: '4 year'
    },
    {
        value: '5-year',
        label: '5 year'
    }
];

// ===========================|| PROFILE 1 - PROFILE ACCOUNT ||=========================== //

const PersonalAccount = ({onBack, onNext}) => {

    const dispatch = useDispatch();
    const navigate = useNavigate()
    const {user, url,  logoutUser} = useAuth()
    const [isLoading, setIsLoading] = useState(false)

    const goHome = () => {
        navigate("/")
    }

    const [currency, setCurrency] = React.useState('Washington');
    const handleChange1 = (event) => {
        setCurrency(event.target.value);
    };

    const [experience, setExperience] = React.useState('Startup');
    const handleChange2 = (event) => {
        setExperience(event.target.value);
    };

    const onSubmit = async (e) => {

        setIsLoading(true)

        if (e.target.full_name.value === "") {
            console.log(user.username)
            e.target.full_name.value = user.username
        } 

        if(e.target.location.value === "") {
            console.log(user.location)
            e.target.location.value = user.location
        } 

        if(e.target.bio.value === "") {
            console.log(user.bio)
            e.target.bio.value = user.bio
        } 

        if(e.target.phone.value === "") {
            console.log(user.phone)
            e.target.phone.value = user.phone
        } 

        if(e.target.email.value === "") {
            console.log(user.email)
            e.target.email.value = user.email
        } 

        if(e.target.web_url.value === "") {
            console.log(user.web_url)
            e.target.web_url.value = user.web_url
        } 

        if(e.target.address.value === "") {
            console.log(user.address)
            e.target.address.value = user.address
        }


        const upload_file = e.target.profile_image.files[0]; 
        console.log("UPLOAD FILE", upload_file)

        e.preventDefault()
        let formdata = new FormData();
        formdata.append('full_name', e.target.full_name.value);
        formdata.append('location', e.target.location.value);
        formdata.append('bio', e.target.bio.value);
        formdata.append('phone', e.target.phone.value);
        formdata.append('email', e.target.email.value);
        formdata.append('web_url', e.target.web_url.value);
        formdata.append('profile_image', upload_file); 
        formdata.append('address', e.target.address.value);

        let requestOptions = {
          method: 'PATCH',
          body: formdata,
          redirect: 'follow'
        };

        console.log("profile form submitted")
        const response = await fetch(`${url}/users/profile/${user.id}`, requestOptions)
          /* .then(response => response.json())
          .then(result => console.log(result))
          .catch(error => console.log('error', error)); */
        /* const response = await fetch(`http://127.0.0.1:8000/users/profile/${user.id}`, requestOptions)
         */

        console.log(response)

        if (response.status === 200) {
            setIsLoading(false)
             dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Profile Updated',
            variant: 'alert',
            alertSeverity: 'success'
        });
        } else {
            setIsLoading(false)
             dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Failed',
            variant: 'alert',
            alertSeverity: 'error'
        });
        } 
    }

    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12} md={6}>
                    <form  onSubmit={onSubmit} action="#" noValidate autoComplete="off">
                        <Grid container spacing={gridSpacing}>
                            <Grid item xs={12} md={6}>
                                <TextField 
                                    id="outlined-basic1" 
                                    fullWidth label="Full Name" 
                                    name="full_name"
                                    type="text"
                                    defaultValue={user.username} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField
                                    id="outlined-basic5"
                                    fullWidth
                                    label="Location"
                                    /* value={currency} */
                                    name="location"
                                    type="text"
                                    defaultValue={user.location}
                                    /* onChange={handleChange1} */
                                >
                                    {/* {currencies.map((option) => (
                                        <MenuItem key={option.value} value={option.value}>
                                            {option.label}
                                        </MenuItem>
                                    ))} */}
                                </TextField>
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    id="outlined-multiline-static1"
                                    label="Bio"
                                    name="bio"
                                    type="text"
                                    multiline
                                    fullWidth
                                    rows={3}
                                    defaultValue={user.bio}
                                />
                            </Grid>
                            {/* <Grid item xs={12} md={6}>
                                <TextField
                                    id="outlined-select-experience"
                                    select
                                    fullWidth
                                    label="Experience"
                                    value={experience}
                                    onChange={handleChange2}
                                >
                                    {experiences.map((option) => (
                                        <MenuItem key={option.value} value={option.value}>
                                            {option.label}
                                        </MenuItem>
                                    ))}
                                </TextField>
                            </Grid> */}
                        {/* </Grid> */}


                        {/* <Grid container spacing={gridSpacing}> */}
                            <Grid item xs={12} md={6}>
                                <TextField 
                                    id="outlined-basic2" 
                                    fullWidth label="Contact Phone" 
                                    name="phone"
                                    type="number"
                                    defaultValue={user.phone} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <TextField 
                                    id="outlined-basic3" 
                                    fullWidth label="Email" 
                                    name="email"
                                    type="email"
                                    defaultValue={user.email} />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField 
                                    id="outlined-basic4" 
                                    fullWidth label="Web Url" 
                                    name="web_url"
                                    type="text"
                                    defaultValue={user.web_url} />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    id="outlined-multiline-static2"
                                    label="Address"
                                    name="address"
                                    type="text"
                                    multiline
                                    fullWidth
                                    rows={3}
                                    defaultValue={user.address}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <input
                                    id="outlined-multiline-static2"
                                    label="Profile Image"
                                    name="profile_image"
                                    type="file"
                                    fullWidth
                                    rows={3}
                                    /* defaultValue="" */
                                />
                            </Grid>
                        
                        <Grid item>
                                <AnimateButton>
                                    <Button type="submit" variant="contained" size="medium" color="secondary">
                                        
                                        {isLoading ? <img src={loader} alt="" style={{width:'25px', height:'25px', marginLeft:'8px'}}/>:'UPDATE PROFILE'}
                                    </Button>
                                </AnimateButton>
                        </Grid>
                        <Grid item xs={12} md={7} lg={8}>
                        <Stack direction="row" spacing={2}>
                        <Button onClick={goHome} variant="contained"  color="primary" startIcon={<KeyboardBackspaceIcon />}>
                            Back Home
                        </Button>
                         <Button type="submit" variant="contained" size="medium" color="secondary" onClick={logoutUser}>
                            Log Out Now
                       </Button>
                       </Stack>
                        </Grid>
                        </Grid>
                    </form>

            </Grid>
            <Grid item xs={12} md={6}>
                {/* <SubCard title="Social Information">
                    <form noValidate autoComplete="off">
                        <Grid container alignItems="center" spacing={gridSpacing} sx={{ mb: '10px' }}>
                            <Grid item>
                                <FacebookIcon />
                            </Grid>
                            <Grid item xs zeroMinWidth>
                                <TextField fullWidth label="Facebook Profile Url" />
                            </Grid>
                            <Grid item>
                                <AnimateButton>
                                    <Button variant="contained" size="small" color="secondary">
                                        Connect
                                    </Button>
                                </AnimateButton>
                            </Grid>
                        </Grid>
                        <Grid container alignItems="center" spacing={gridSpacing} sx={{ mb: '10px' }}>
                            <Grid item>
                                <TwitterIcon />
                            </Grid>
                            <Grid item xs zeroMinWidth>
                                <TextField fullWidth label="Twitter Profile Url" />
                            </Grid>
                            <Grid item>
                                <AnimateButton>
                                    <Button variant="contained" size="small" color="secondary">
                                        Connect
                                    </Button>
                                </AnimateButton>
                            </Grid>
                        </Grid>
                        <Grid container alignItems="center" spacing={gridSpacing}>
                            <Grid item>
                                <LinkedInIcon />
                            </Grid>
                            <Grid item xs zeroMinWidth>
                                <TextField fullWidth label="LinkedIn Profile Url" />
                            </Grid>
                            <Grid item>
                                <AnimateButton>
                                    <Button variant="contained" size="small" color="secondary">
                                        Connect
                                    </Button>
                                </AnimateButton>
                            </Grid>
                        </Grid>
                    </form>
                </SubCard> */}
            </Grid>
        </Grid>
    );
};

export default PersonalAccount;
