import PropTypes from 'prop-types';
import React, {useState} from 'react';
import { useDispatch, useSelector } from 'react-redux'; 
import useAuth from 'hooks/useAuth'


// material-ui
import { styled } from '@material-ui/styles';
import { Grid, Tab, Tabs, Typography } from '@material-ui/core';

// project imports
import CartEmpty from './CartEmpty';
import Order from './Order';
import PersonalAccount from './PersonalAccount';
import Payment from './Payment';
import MainCard from 'ui-component/cards/MainCard';
import axios from 'utils/axios';
import {
    BACK_STEP,
    NEXT_STEP,
    REMOVE_PRODUCT,
    SET_STEP,
    SNACKBAR_OPEN,
    UPDATE_QUANTITY,
    SET_BILLING_ADDRESS,
    SET_SHIPPING_CHARGE
} from 'store/actions';
import { gridSpacing } from 'store/constant';

// assets
import OpenInNewTwoToneIcon from '@material-ui/icons/OpenInNewTwoTone';
import ApartmentIcon from '@material-ui/icons/Apartment';
import CreditCardTwoToneIcon from '@material-ui/icons/CreditCardTwoTone';

const StyledTab = styled((props) => <Tab {...props} />)(({ theme, customization, value, cart }) => ({
    color: cart.checkout.step >= value ? theme.palette.success.dark : theme.palette.grey[600],
    minHeight: 'auto',
    minWidth: '250px',
    padding: 16,
    borderRadius: `${customization.borderRadius}px`,
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'flex-start',
    textAlign: 'left',
    justifyContent: 'flex-start',
    '&.Mui-selected': {
        color: theme.palette.primary.main,
        background: theme.palette.mode === 'dark' ? theme.palette.dark.main : theme.palette.grey[50]
    },
    '& > svg': {
        marginBottom: '0px !important',
        marginRight: 10,
        marginTop: 2,
        height: '20px',
        width: '20px'
    },
    [theme.breakpoints.down('sm')]: {
        minWidth: '100%'
    }
}));

const initialValues = {
    id: '',
    products: '',
    quantity: '',
    payment: '',
    amount: '',
    status: '',
    vendor: '',
}

// tabs option
const tabsOption = [
    {
        label: 'Customer Profile',
        icon: <ApartmentIcon />,
        caption: 'Edit Your Profile'
    },
    {
        label: 'Track Orders',
        icon: <OpenInNewTwoToneIcon />,
        caption: 'All Your Orders'
    }/*,
    {
        label: 'Payment',
        icon: <CreditCardTwoToneIcon />,
        caption: 'Add & Update Card'
    } */
];

// tabs
function TabPanel({ children, value, index, ...other }) {
    return (
        <div role="tabpanel" hidden={value !== index} id={`simple-tabpanel-${index}`} aria-labelledby={`simple-tab-${index}`} {...other}>
            {value === index && <div>{children}</div>}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

// ===========================|| PRODUCT - CHECKOUT MAIN ||=========================== //

const Checkout = () => {

    // additions
    const [values, setValues] = useState(initialValues)
    const { url } = useAuth()
    
    

    

    const customization = useSelector((state) => state.customization);
    const cart = useSelector((state) => state.cart);
    const dispatch = useDispatch();

    const isCart = cart.checkout.products && cart.checkout.products.length > 0;

    const [value, setValue] = React.useState(cart.checkout.step > 2 ? 2 : cart.checkout.step);
    const [billing, setBilling] = React.useState(cart.checkout.billing);
    const [Orders, setOrders] = React.useState([]);

    const getOrders = async () => {

        var requestOptions = {
          method: 'GET',
          redirect: 'follow'
        };

        const res = await fetch(`${url}/users/address/`, requestOptions)
        console.log('RES')
        console.log(res)
        const data = await res.json()

        setOrders(data);
    };

    React.useEffect(() => {
        getOrders();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const handleChange = (event, newValue) => {
        setValue(newValue);
        dispatch({ type: SET_STEP, step: newValue });
    };

    React.useEffect(() => {
        setValue(cart.checkout.step > 2 ? 2 : cart.checkout.step);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [cart.checkout.step]);

    /* const removeProduct = (id) => {
        dispatch({ type: REMOVE_PRODUCT, id });
        dispatch({
            type: SNACKBAR_OPEN,
            open: true,
            message: 'Update Cart Success',
            variant: 'alert',
            alertSeverity: 'success'
        });
    }; */



    const onNext = () => {
        dispatch({ type: NEXT_STEP });
    };

    const onBack = () => {
        dispatch({ type: BACK_STEP });
    };

    /* const billingAddressHandler = (address) => {
        if (billing !== null || address !== null) {
            if (address !== null) {
                setBilling(address);
            }

            dispatch({ type: SET_BILLING_ADDRESS, address: address !== null ? address : billing });
            onNext();
        } else {
            dispatch({
                type: SNACKBAR_OPEN,
                open: true,
                message: 'Please select delivery address',
                variant: 'alert',
                alertSeverity: 'error'
            });
        }
    };

    const handleShippingCharge = (type) => {
        dispatch({ type: SET_SHIPPING_CHARGE, value: type });
    }; */

    return (
        <MainCard>
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Tabs
                        value={value}
                        onChange={handleChange}
                        aria-label="icon label tabs example"
                        variant="scrollable"
                        sx={{
                            '& .MuiTabs-flexContainer': {
                                borderBottom: 'none'
                            },
                            '& .MuiTabs-indicator': {
                                display: 'none'
                            },
                            '& .MuiButtonBase-root + .MuiButtonBase-root': {
                                position: 'relative',
                                overflow: 'visible',
                                ml: '16px',
                                '&:after': {
                                    content: '""',
                                    bgcolor: '#ccc',
                                    width: '1px',
                                    height: 'calc(100% - 16px)',
                                    position: 'absolute',
                                    top: '8px',
                                    left: '-8px'
                                }
                            }
                        }}
                    >
                        {tabsOption.map((tab, index) => (
                            <StyledTab
                                customization={customization}
                                value={index}
                                cart={cart}
                                /* disabled={index > cart.checkout.step} */
                                key={index}
                                icon={tab.icon}
                                label={
                                    <Grid container direction="column">
                                        <Typography variant="subtitle1" color="inherit">
                                            {tab.label}
                                        </Typography>
                                        <Typography component="div" variant="caption" sx={{ textTransform: 'capitalize' }}>
                                            {tab.caption}
                                        </Typography>
                                    </Grid>
                                }
                            />
                        ))}
                    </Tabs>
                </Grid>
                <Grid item xs={12}>
                    <TabPanel value={value} index={0}>
                        <PersonalAccount onBack={onBack} onNext={onNext}/>
                    </TabPanel>
                    <TabPanel value={value} index={1}>
                        
                            <Order checkout={cart.checkout} onNext={onNext} />
                    
                    </TabPanel>{/*
                    <TabPanel value={value} index={2}>
                        <Payment checkout={cart.checkout} onBack={onBack} onNext={onNext} handleShippingCharge={handleShippingCharge} values={values}/>
                    </TabPanel> */}
                </Grid>
            </Grid>
        </MainCard>
    );
};

export default Checkout;
