// ===========================|| CART - MAIN ||=========================== //

const Order = ({ checkout, onNext }) => {
    const theme = useTheme();
    const matchDownMD = useMediaQuery(theme.breakpoints.down('md'));
    const totalQuantity = sum(checkout.products.map((item) => item.quantity));
    const [rows, setRows] = React.useState([]);
    const [vendor, setVendor] = React.useState([]);

    const { user,url } = useAuth();

    /* for (var i = 0; i < rows.length; i++) {
        var requestOptions = {
          method: 'GET',
          redirect: 'follow'
        };

        const res = await fetch(`${url}/${rows[i].payee_name}`, requestOptions)
        const data = await res.json()

        vendor.append(data)
        
    } */


    const getOrders = async() => {
        var requestOptions = {
          method: 'GET',
          redirect: 'follow'
        };

        const res = await fetch(`${url}/api/customer-vendor-order/?search=${user.username}`, requestOptions)
        const data = await res.json()

        setRows(data)
    }
    
    const pullVendor = async(payee_name) => {
        var requestOptions = {
          method: 'GET',
          redirect: 'follow'
        };

        const res = await fetch(`${url}/users/profile/${payee_name}`, requestOptions)
        const data = await res.json()

        setVendor(data)
    }



    React.useEffect(() => {
        getOrders();
    }, []);

    return (
        <Grid container spacing={gridSpacing}>
            {/* <Grid item xs={12}>
                <Stack direction="row" alignItems="center" spacing={1}>
                    <Typography variant="subtitle1">Cart Item</Typography>
                    <Typography variant="caption" sx={{ fontSize: '0.875rem' }}>
                        ({totalQuantity})
                    </Typography>
                </Stack>
            </Grid> */}
            <Grid item xs={12}>
                <TableContainer>
                    <Table sx={{ minWidth: 650 }} aria-label="simple table">
                        <TableHead
                            sx={{
                                borderTop: '1px solid',
                                color: theme.palette.mode === 'dark' ? theme.palette.dark.light + 15 : 'grey.200'
                            }}
                        >
                            <TableRow style={{backgroundColor: '#40ae49'}}>
                                <TableCell>ID</TableCell>
                                <TableCell align="center">Products</TableCell>
                                <TableCell align="center">Quantity</TableCell>
                                <TableCell align="center">Total</TableCell>
                                <TableCell align="center">Payment</TableCell>
                                <TableCell align="center">Vendor</TableCell>
                                <TableCell align="center">Status</TableCell>
                                <TableCell align="center" />


                                
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map((row, index) => {
                                /* const payee_name = row.payee_name
                                pullVendor(payee_name) */
                                
                                return (
                                    <TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                        <TableCell /* component="th" scope="row" */>
                                            <Grid container alignItems="center" spacing={2}>
                                                <Grid item>
                                                    <Typography variant="subtitle1">{row.id}</Typography>
                                               </Grid>
                                            </Grid>
                                        </TableCell>
                                        <TableCell align="center">
                                            
                                            <Typography variant="subtitle1">
                                               {row.products}
                                            </Typography> 
                                                                            
                                        </TableCell>
                                        <TableCell align="center">
                                          
                                            <Typography variant="subtitle1">
                                                   {row.quantity}
                                            </Typography>
                                 
                                        </TableCell>
                                        <TableCell align="center">
                                            <Typography variant="subtitle1">
                                                {row.purchase_amount * row.quantity}
                                            </Typography>
                                        </TableCell>
                                        <TableCell align="center">
                                            <Typography variant="subtitle1">
                                                   {row.payment_type}
                                            </Typography>
                                        </TableCell>
                                        <TableCell align="center">
                                            <Typography variant="subtitle1">
                                                   {row.payee_name}
                                            </Typography>
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.status === true && <Chip label="Complete" size="small" color="success" />}
                                            {/* {row.status === 2 && <Chip label="Pending" size="small" color="warning" />} */}
                                            {row.status === false && <Chip label="Processing" size="small" color="primary" />}
                                        </TableCell>
                                    </TableRow>
                                );
                            })}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Grid>
            {/* <Grid item xs={12}>
                <OrderSummary checkout={checkout} />
            </Grid> */}
            <Grid item xs={12}>
                <Grid direction={matchDownMD ? 'column-reverse' : 'row'} container spacing={3} alignItems={matchDownMD ? '' : 'center'}>
                    <Grid item xs={12} md={7} lg={8}>
                        <Button component={Link} to="/" variant="string" startIcon={<KeyboardBackspaceIcon />}>
                            Back Home
                        </Button>
                    </Grid>
                    {/* <Grid item xs={12} md={5} lg={4}>
                        <Stack spacing={gridSpacing}>
                            <CartDiscount />
                            <Button variant="contained" fullWidth onClick={onNext}>
                                Check Out
                            </Button>
                        </Stack>
                    </Grid> */}
                </Grid>
            </Grid>
        </Grid>
    );
};

Order.propTypes = {
    checkout: PropTypes.object,
    updateQuantity: PropTypes.func,
    removeProduct: PropTypes.func,
    onNext: PropTypes.func
};

export default Order;
